.footer-content {
  padding: 40px 0;
  .wrapper {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    .items {
      width: 100%;
      .item {
        display: flex;
        font-size: 12px;
        line-height: 34px;
        opacity: 0.8;
        &:first-child {
          font-weight: 600;
          font-size: 12px;
          line-height: 13px;
          display: flex;
          align-items: center;
          letter-spacing: 0.06em;
          text-transform: uppercase;
          padding-bottom: 8px;
        }
      }
    }
  }
}
